import { Grid, Button } from '@material-ui/core';
import React from 'react';
import './App.css';
import ResponsiveGallery from 'react-responsive-gallery';

export default function AboutPage() {

  const FlashAfr = [
    { src: "https://nexsabio.in/wp-content/uploads/2021/07/nexsa-9001_page-0001.jpg", name: "image1" }
];

  return (
    <div style={{ marginTop: '3%',display: 'inline-block' }}>
      <div className="cards-div">
        <div className="header1">About Us</div>
        <Grid container>
          <Grid item xs={4} sm={4} md={4} lg={4}>
            
            <img src={'https://nexsabio.in/wp-content/uploads/2020/10/nexsa-bio.jpg'/*mansmall1*/} alt="clipping" className="headimg1" />
            
          </Grid>
          <Grid item xs={8} sm={8} md={8} lg={8}>
          <div className="subtitle1">
          <strong>NEXSA BIO </strong>  is one of the experts in manufacturing of bio septic tank. Our motto is to provide a pollution free environment and clean ground water. we are providing long term sollution for sewage problem for residence as well as commercial. Our bio septic tank is used to process and dissolve all form of organic waste .especially human wastage. It help us to prevent ground water pollution, which treats (recycle) water before evaporation and perculation, prevents bad smell from sewage water. We can use treated water for irrigation/gardening purpose. Our aspiration is to conserve the environment through a better prevention of sewage pollution and preserve clean ground water
              {/* <Button color="primary" style={{ background: "#0D84FB", color: "#fff", padding: "2%" }}>Know more</Button> */}
            </div>
          </Grid>
        </Grid>
        <h2 >Certificate</h2>
        <Grid container>
          <Grid item xs={4} sm={4} md={4} lg={4}></Grid>
          <Grid item xs={4} sm={4} md={4} lg={4}>
          <ResponsiveGallery useLightBox={true} images={FlashAfr} screenWidthSizes={{xs: 420,s: 600,m: 500,l: 500,xl: 500}}
                        imageMaxWidth={{xs: 500,s: 500,m: 500,l: 500,xl: 500,xxl:500}}
                        numOfImagesPerRow={{xs: 1,s: 1,m: 1,l: 1,xl: 1, xxl:1}}
                        />   
          </Grid>
          
          <Grid item xs={4} sm={4} md={4} lg={4}></Grid>
           
        </Grid>
      </div>
      
      
    </div>
  )
}
