
import React, { useEffect} from 'react';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { withRouter } from 'react-router-dom';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import CancelPresentationIcon from '@material-ui/icons/CancelPresentation';
import {FormGroup,FormControlLabel, Switch} from '@material-ui/core';
import { styled } from '@mui/material/styles';

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },    
  },
  sectionMobile: {
    display: 'block',
    // [theme.breakpoints.up('md')]: {
    //   display: 'none',
    // },
  },
}));

const MaterialUISwitch = styled(Switch)(({ theme }) => ({
  width: 62,
  height: 34,
  padding: 7,
  '& .MuiSwitch-switchBase': {
    margin: 1,
    padding: 0,
    transform: 'translateX(6px)',
    '&.Mui-checked': {
      color: '#fff',
      transform: 'translateX(22px)',
      '& .MuiSwitch-thumb:before': {
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
          '#fff',
        )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`,
      },
      '& + .MuiSwitch-track': {
        opacity: 1,
        backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
      },
    },
  },
  '& .MuiSwitch-thumb': {
    backgroundColor: theme.palette.mode === 'dark' ? '#003892' : '#001e3c',
    width: 32,
    height: 32,
    '&:before': {
      content: "''",
      position: 'absolute',
      width: '100%',
      height: '100%',
      left: 0,
      top: 0,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
        '#fff',
      )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`,
    },
  },
  '& .MuiSwitch-track': {
    opacity: 1,
    backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
    borderRadius: 20 / 2,
  },
}));

function Header(props) {
  const classes = useStyles();  

  function myFunction(e,type) {     
      props.history.push(type);
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      closeNav();
  }  

  function openNav() {
    document.getElementById("mySidenav").style.width = "250px";
  }
  
  function closeNav() {
    document.getElementById("mySidenav").style.width = "0";
  }

  return (
    <>
      <div id="mySidenav" className="sidenav">              
              <IconButton edge="start" className="closebtn" style={{color:"#fff"}} onClick={() =>openNav()}>
                <CancelPresentationIcon />
              </IconButton>
              <Button size="small" className={"HeaderBtn "+(window.location.href.indexOf("home") > -1 ? "HeaderBtn-active" : "")}  id="HomeBtn" onClick={(e)=>myFunction(e,"home")}>
                  Home
              </Button>
              <Button size="small" className={"HeaderBtn "+(window.location.href.indexOf("about") > -1 ? "HeaderBtn-active" : "")} onClick={(e)=>myFunction(e,"about")}>
                  About Us
              </Button>
              <Button size="small" className={"HeaderBtn "+(window.location.href.indexOf("service") > -1 ? "HeaderBtn-active" : "")} onClick={(e)=>myFunction(e,"service")}>
                  Services
              </Button>
              <Button size="small" className={"HeaderBtn "+(window.location.href.indexOf("gallery") > -1 ? "HeaderBtn-active" : "")} onClick={(e)=>myFunction(e,"gallery")}>
                  Gallery
              </Button>
              <Button size="small" className={"HeaderBtn "+(window.location.href.indexOf("contact-us") > -1 ? "HeaderBtn-active" : "")} onClick={(e)=>myFunction(e,"contact-us")}>
                  Contact Us
              </Button>
      </div>
      <div className={classes.grow} >      
        <AppBar position="fixed" id="main-header" className="main-header">
          <Toolbar>
            <div style={{display:'flex',alignItems: 'center',width:'90%'}}>              
              
              <FormGroup>
                <FormControlLabel
                  control={<MaterialUISwitch onChange={(e) => {document.body.classList.toggle("dark-mode");}} sx={{ m: 1 }} />}
                  label="Dark Mode"
                />
              </FormGroup>
              {/* <p className="main-title">Nexsa Bio</p> */}
              <img width="120" height="40" style={{margin: 'auto'}} src="https://nexsabio.in/wp-content/uploads/2020/09/cropped-log-2.jpg" class="custom-logo" alt="Bio septic tank in Chennai | Nexsa bio septic tank manufacturer chennai | Bio septic tank in tamilnadu | Best bio septic tank near me"/>
            </div>
            <div className={classes.grow} />
            <div className={classes.sectionMobile}>
                <IconButton edge="start" className="menuicon" onClick={() =>openNav()}>
                  <MenuIcon />
                </IconButton>     
            </div>   
          </Toolbar>
        </AppBar>
      </div>
    </>
  );
}
export default withRouter(Header);
